export const createAccountForm = document.createElement("form");
export const createAccountFormUserName = document.createElement("input");
export const createAccountFormPassword = document.createElement("input");
export const createAccountFormBtn = document.createElement("button");
createAccountForm.setAttribute("id", "create-user-form");
createAccountFormUserName.setAttribute("id", "create-user-name-form");
createAccountFormUserName.setAttribute("type", "text");
createAccountFormUserName.setAttribute("placeholder", "Enter your name");
createAccountFormPassword.setAttribute("id", "create-user-password-form");
createAccountFormPassword.setAttribute("name", "create-user-password-form");
createAccountFormPassword.setAttribute("type", "password");
createAccountFormPassword.setAttribute("placeholder", "Enter your password");
createAccountFormBtn.setAttribute("id", "createAccountBtn");
createAccountFormBtn.textContent = "Create Account";
