import "./styles.css";
import {
  createAccountForm,
  createAccountFormUserName,
  createAccountFormPassword,
  createAccountFormBtn
} from "./createAccountForm";

import {
  loginForm,
  loginFormUserName,
  loginFormUserPassword,
  loginFormBtn
} from "./loginAccountForm";

function input(inputName, inputDefaultValue) {
  let input = prompt(inputName, inputDefaultValue);
  return input;
}

function textResponse(res) {
  const textElement = document.createElement("h2");
  textElement.textContent = res;
  textElement.style.fontsize = "2rem";
  textElement.style.margin = "1rem";
  appElement.append(textElement);
  loginForm.style.display = "none";
}

const appElement = document.getElementById("app");

createAccountForm.append(createAccountFormUserName);
createAccountForm.append(createAccountFormPassword);
createAccountForm.append(createAccountFormBtn);
appElement.append(createAccountForm);

createAccountForm.addEventListener("submit", (e) => {
  e.preventDefault();
  console.log("Account was Sucessfully created");
  loginForm.append(loginFormUserName);
  loginForm.append(loginFormUserPassword);
  loginForm.append(loginFormBtn);
  createAccountForm.style.display = "none";
  appElement.append(loginForm);
  loginForm.addEventListener("submit", (e) => {
    e.preventDefault();
    if (
      loginFormUserName.value === createAccountFormUserName.value &&
      loginFormUserPassword.value === createAccountFormPassword.value
    ) {
      terminal();
      function terminal() {
        let cli = input("~$", "pwd");
        switch (cli) {
          case "pwd":
            loginForm.style.display = "none";
            textResponse("~$");
            openTerminalBtn();
            break;
          case "gUserName":
            let password_to_gUserName = input("Password", "");
            if (password_to_gUserName == loginFormUserPassword.value) {
              textResponse(`User Name: ${loginFormUserName.value}`);
              openTerminalBtn();
            } else {
              textResponse("Please Enter correct password");
              openTerminalBtn();
            }
            break;
          case "gPassword":
            let s_code = input("Serect Code", "");
            let s_code_enter = input("Serect Code Enter", "");
            if (s_code_enter == s_code) {
              textResponse(`Password: ${loginFormUserPassword.value}`);
              openTerminalBtn();
            } else {
              textResponse("Please Enter correct Serect Code");
              openTerminalBtn();
            }
            break;
          case "sUserName":
            let password_to_sUserName = input("Password", "");
            if (password_to_sUserName == loginFormUserPassword.value) {
              let currentUserName = input("Current User Name", "");
              if (currentUserName == loginFormUserName.value) {
                let newUserName = input("New User Name", "");
                if (newUserName != loginFormUserName.value) {
                  loginFormUserName.value = newUserName;
                  createAccountFormUserName.value = newUserName;
                  textResponse("User Name Succesfully changed");
                  openTerminalBtn();
                } else {
                  textResponse("Please Enter new User Name");
                  openTerminalBtn();
                }
              } else {
                textResponse("Please enter current user name");
                openTerminalBtn();
              }
            } else {
              textResponse("Please Enter Password Correct");
              openTerminalBtn();
            }
            break;
          case "sPassword":
            let password_to_sPassword = input("Password", "");
            if (password_to_sPassword == loginFormUserPassword.value) {
              let currentPassword = input("Current Password", "");
              if (currentPassword == loginFormUserPassword.value) {
                let newPassword = input("New Password", "");
                if (newPassword != loginFormUserPassword.value) {
                  loginFormUserPassword.value = newPassword;
                  createAccountFormPassword.value = newPassword;
                  textResponse("Password is Sucessfully changed");
                  openTerminalBtn();
                } else {
                  textResponse("Please Enter new Password");
                  openTerminalBtn();
                }
              } else {
                textResponse("Please enter current password");
                openTerminalBtn();
              }
            } else {
              textResponse("Please enter password correct");
              openTerminalBtn();
            }
            break;
          case "time":
            let time = new Date();
            textResponse(`${time}`);
            break;
          default:
            textResponse(`Command ${cli} not founded`);
            openTerminalBtn();
        }

        function openTerminalBtn() {
          const openTerminalBtn = document.createElement("button");
          openTerminalBtn.textContent = "Open Terminal";
          openTerminalBtn.style.border = "none";
          openTerminalBtn.style.padding = "1rem";
          openTerminalBtn.style.margin = "1rem";
          openTerminalBtn.style.background = "red";
          openTerminalBtn.style.color = "white";
          openTerminalBtn.addEventListener("click", terminal);
          appElement.append(openTerminalBtn);
          loginForm.style.display = "none";
        }
        console.log("terminal is oppend");
      }
      console.log("Logged in");
    } else {
      console.log("please enter your user name and password correct");
    }
  });
});
