export const loginForm = document.createElement("form");
export const loginFormUserName = document.createElement("input");
export const loginFormUserPassword = document.createElement("input");
export const loginFormBtn = document.createElement("button");
loginForm.setAttribute("id", "loginForm");
loginFormUserName.setAttribute("name", "login-user-name");
loginFormUserName.setAttribute("id", "login-user-name");
loginFormUserPassword.setAttribute("type", "text");
loginFormUserName.setAttribute("placeholder", "Enter your name");
loginFormUserPassword.setAttribute("name", "login-user-password");
loginFormUserPassword.setAttribute("id", "login-user-password");
loginFormUserPassword.setAttribute("type", "password");
loginFormUserPassword.setAttribute("placeholder", "Enter your password");
loginFormBtn.setAttribute("id", "loginFormBtn");
loginFormBtn.textContent = "Login";
